

.page-cont {
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: #1c1c1c; /* Same background color as SignUpForm */
  }
  
  .form-cont {
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    max-width: 400px;
    padding: 20px;
    background-color: #1c1c1c; /* Same background color as SignUpForm */
    border-radius: 5px;
    box-shadow: 10px 10px 100px 10px rgba(255, 255, 255, 0.1);
    margin-bottom: 20px;
  }
  
  .app-ttle{
    margin-bottom: 20px;
    color: red;
    border: 2px solid red;
  }
  
  .group-cont {
    margin-bottom: 15px;
  }
  
  .group-cont input {
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    margin-bottom: 15px;
    width: 100%;
    padding: 10px;
    border: none;
    border-radius: 5px;
    box-sizing: border-box;
    font-size: 16px;
  }
  
  .group-cont input:hover {
    border: 2px solid red;
  }
  
  .login-login {
   width: 100%;
  padding: 10px;
  background-color: rgb(255, 0, 0);
  border: none;
  border-radius: 5px;
  color: #fff;
  font-size: 16px;
  cursor: pointer;
  margin-bottom: 15px;
  }
  
  .group-cont button:hover {
    background-color: #000000;
  }
  
  .group-cont input::placeholder {
    color: lightgray;
  }
  
  